import React, { useContext, useState, useEffect } from 'react';
import storage from "local-storage-fallback";
import NewsAPI from 'newsapi';
const NewsContext = React.createContext();


const MinimalNewContext = (props) => {

    const getInitialTheme = () => {
        const savedTheme = storage.getItem("theme")
        return savedTheme ? JSON.parse(savedTheme) : { mode: "light" }
    }

    const [isSideBarOpen, setIsSideBarOpen] = useState(false);
    const [theme, setTheme] = useState(getInitialTheme)
    const [fetchError, setFetchError] = useState("");
    const [isLoading, setLoading] = useState(true);
    const [pageTitle, setPageTitle] = useState("");

    const [currentArticleIndex, setCurrentArticleIndex] = useState(0)
    const [topHeadlines, setTopheadlines] = useState({});
    const [numberOfArticles, setNumberOfArticles] = useState();

    useEffect(() => {
        document.getElementsByTagName('body')[0].classList.remove('preload')
    }, [])
    useEffect(() => {
        storage.setItem("theme", JSON.stringify(theme));
    }, [theme]);

    const onSearchClick = (keyword, domains) => {
        setIsSideBarOpen(false)
        const newsapi = new NewsAPI('6b5c258e98f140ac864a36a43aa83a41')
        newsapi.v2.everything({
            q: keyword,
            domains: domains,
            language: 'en',
            sortBy: 'publishedAt',
            page: 1
        }).then(everything => {
            setTopheadlines(everything)
            setNumberOfArticles(everything.articles.length);
            setCurrentArticleIndex(0)
            setPageTitle(domains.length > 0 ? `All articles about ${keyword} from ${domains.toString()}` : `All articles about ${keyword}`)
            setFetchError("")

        }).catch(error => {
            console.log(error)
            setLoading(false);
            setFetchError(error);
        });
    }

    const onNavigateArticle = (mousePosition, e) => {
        e.stopPropagation();
        if (e.target.id === 'body') {
            if (mousePosition === "s-resize") {
                if (currentArticleIndex === numberOfArticles - 1) {
                    setCurrentArticleIndex(0);
                }
                else {
                    setCurrentArticleIndex(currentArticleIndex + 1);
                }
            } else {
                if (currentArticleIndex === 0) {
                    setCurrentArticleIndex(numberOfArticles-1)
                } else {
                    setCurrentArticleIndex(currentArticleIndex - 1);
                }
            }
        }
    }

    return <NewsContext.Provider value={{ theme, setTheme, isSideBarOpen, setIsSideBarOpen, fetchError, setFetchError, isLoading, setLoading, onNavigateArticle, currentArticleIndex, setCurrentArticleIndex, topHeadlines, setTopheadlines, numberOfArticles, setNumberOfArticles, onSearchClick, pageTitle, setPageTitle }}>
        {props.children}
    </NewsContext.Provider>
}

export const useNewsContext = () => {
    return useContext(NewsContext)
}


export default MinimalNewContext;